import provinceData from './city-data/province.js';
import cityData from './city-data/city.js';
import areaData from './city-data/area.js';
import {
	getChildrenByPId
} from '@/api/userApi.js'
export default {
	data() {
		return {
			pickerValue: [0, 0, 0],
			provinceDataList: [],
			cityDataList: [],
			areaDataList: [],
			/* 是否显示控件 */
			showPicker: false,
			myAddressSlots: [],
			ids:[]
		};
	},
	created() {
		this.getProvinceArr(0)
		this.init()
	},
	props: {
		/* 默认值 */
		pickerValueDefault: {
			type: Array,
			default () {
				return [0, 0, 0]
			}
		},
		hasArea:{
			type:Boolean,
			default: true
		},
		/* 主题色 */
		themeColor: String
	},
	watch: {
		pickerValueDefault() {
			this.init();
		}
	},
	methods: {
		// 遍历json，返回省级对象数组
		getProvinceArr(id) {
			let data = {
				parentId: id,
				isLoading:1
			}
			getChildrenByPId(data).then(res => {
				if (res.code === '1000') {
					this.provinceDataList = res.data
					let data1 = {
						parentId: this.provinceDataList[0].id
					}
					getChildrenByPId(data1).then(res => {
						if (res.code === '1000') {
							this.cityDataList = res.data
							let data2 = {
								parentId: this.cityDataList[0].id
							}
							getChildrenByPId(data2).then(res => {
								if (res.code === '1000') {
									this.areaDataList = res.data


								}
							})

						}
					})

				}
			})
		},
		init() {
			this.handPickValueDefault(); // 对 pickerValueDefault 做兼容处理
			const pickerValueDefault = this.pickerValueDefault
			this.cityDataList = cityData[pickerValueDefault[0]];
			this.areaDataList = areaData[pickerValueDefault[0]][pickerValueDefault[1]];
			this.pickerValue = pickerValueDefault;

		},
		show() {
			setTimeout(() => {
				this.showPicker = true;
			}, 0);
		},
		maskClick() {
			this.pickerCancel();
		},
		pickerCancel() {
			this.showPicker = false;
			this._$emit('onCancel');
		},
		pickerConfirm(e) {
			this.showPicker = false;
			this._$emit('onConfirm');
		},
		showPickerView() {
			this.showPicker = true;
		},
		handPickValueDefault() {
			const pickerValueDefault = this.pickerValueDefault

			let provinceIndex = pickerValueDefault[0]
			let cityIndex = pickerValueDefault[1]
			const areaIndex = pickerValueDefault[2]
			if (
				provinceIndex !== 0 ||
				cityIndex !== 0 ||
				areaIndex !== 0
			) {
				if (provinceIndex > provinceData.length - 1) {
					this.pickerValueDefault[0] = provinceIndex = provinceData.length - 1;
				}
				if (cityIndex > cityData[provinceIndex].length - 1) {
					this.pickerValueDefault[1] = cityIndex = cityData[provinceIndex].length - 1;
				}
				if (areaIndex > areaData[provinceIndex][cityIndex].length - 1) {
					this.pickerValueDefault[2] = areaData[provinceIndex][cityIndex].length - 1;
				}
			}
		},
		pickerChange(e) {
			let changePickerValue = e.mp.detail.value;
			if (this.pickerValue[0] !== changePickerValue[0]) {
				// 第一级发生滚动

				let data1 = {
					parentId: this.provinceDataList[changePickerValue[0]].id,
					isLoading: 1
				}
				getChildrenByPId(data1).then(res => {
					if (res.code === '1000') {
						this.cityDataList = res.data
						let data2 = {
							parentId: this.cityDataList[changePickerValue[1]].id,
							isLoading: 1
						}
						getChildrenByPId(data2).then(res => {
							if (res.code === '1000') {
								this.areaDataList = res.data


							}
						})

					}
				})


				//this.cityDataList = cityData[changePickerValue[0]];
				//this.areaDataList = areaData[changePickerValue[0]][0];
				changePickerValue[1] = 0;
				changePickerValue[2] = 0;
			} else if (this.pickerValue[1] !== changePickerValue[1]) {
				// 第二级滚动
				// this.areaDataList =areaData[changePickerValue[0]][changePickerValue[1]];
				let data = {
					parentId: this.cityDataList[changePickerValue[1]].id,
					isLoading: 1
				}
				getChildrenByPId(data).then(res => {
					if (res.code === '1000') {
						this.areaDataList = res.data


					}
				})
				changePickerValue[2] = 0;
			}
			this.pickerValue = changePickerValue;
			this.ids = [this.provinceDataList[this.pickerValue[0]].id,this.cityDataList[this.pickerValue[1]].id,this.areaDataList[this.pickerValue[2]].id]
			this._$emit('onChange');
		},
		_$emit(emitName) {
			let pickObj = {
				label: this._getLabel(),
				value: this.pickerValue,
				cityCode: this._getCityCode(),
				ids: this.ids
			};
			this.$emit(emitName, pickObj);
		},
		_getLabel() {
			let pcikerLabel =
				this.provinceDataList[this.pickerValue[0]].name +
				'-' +
				this.cityDataList[this.pickerValue[1]].name +
				'-' +
				this.areaDataList[this.pickerValue[2]].name;
			return pcikerLabel;
		},
		_getCityCode() {
			return this.areaDataList[this.pickerValue[2]].id;
		}
	}
};
